import { lazy, useContext } from "react";
import { Navigate, Route, Routes } from "react-router-dom";
import { AuthContext } from "../context";
import { PrivatePaths } from "./path";
import OutletWrapper from "../modules/Auth/OutletWrapper";

const paths = [
	{
		path: "",
		element: lazy(() => import("../modules/Home")),
	},
	{
		path: "*",
		element: lazy(() => import("../modules/NotFound")),
	},
	{
		path: "Qr-clock-in",
		element: lazy(() => import("../modules/QRCode/Clockin/index")),
	},
	{
		path: "Qr-clock-out",
		element: lazy(() => import("../modules/QRCode/Clockout/index")),
	},
];

const authPaths = [
	{
		path: "login",
		element: lazy(() => import("../modules/Auth/Login")),
	},
	{
		path: "register",
		element: lazy(() => import("../modules/Auth/Register")),
	},
	{
		path: "admin-login",
		element: lazy(() => import("../modules/Auth/AdminLogin/Login")),
	},
	{
		path: "verify-account/:token",
		element: lazy(() => import("../modules/Auth/VerifyAccount")),
	},
	{
		path: "forgot-password",
		element: lazy(() => import("../modules/Auth/ForgotPassword")),
	},
	{
		path: "reset-password/:id",
		element: lazy(() => import("../modules/Auth/ResetPassword")),
	},
	{
		path: "clock-in",
		element: lazy(() =>
			import("../modules/Auth/FacialRecognition/Clockin/index")
		),
	},
	{
		path: "clock-out",
		element: lazy(() =>
			import("../modules/Auth/FacialRecognition/Clockout/index")
		),
	},
];

function Auth() {
	const { user } = useContext(AuthContext);
	if (user) {
		return <Navigate to={`${PrivatePaths.ADMIN_DASHBOARD}`} replace />;
	}

	return (
		<Routes>
			{paths.map(({ path, element: Element }) => (
				<Route key={path} path={path} element={<Element />} />
			))}
			<Route element={<OutletWrapper />}>
				{authPaths.map(({ path, element: Element }) => (
					<Route key={path} path={path} element={<Element />} />
				))}
			</Route>
		</Routes>
	);
}

export default Auth;
